

@keyframes example {
    from {width: 0;}

    to {width:100%;}

  }


  .header
  {
    position:sticky;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    background-color: $color-black-dark;
    border-bottom: 2px solid;
    height: 100px;
    padding-top: 2%;

   

  nav{
    width: 100%;
    text-align: center;

    h3{
      color: $color-white;
      font-family: $font-title;
      padding: 0;
      margin: 0;
    }

      ul{
        list-style: none;
        justify-items: center;
        justify-content: space-between;

        li{
          display: inline;
          margin: 0 7% 0 7%;
          
          font-family:$font-title;

          
          
          a{
            color: $color-white;
            text-decoration:dashed;
            font-family: $font-title;

          }

          a:hover
          {
            text-decoration:dashed;
            text-shadow: 1px 1px 2px blue;
          }

          

        }
      }
  }

  .name:hover {
    
    font-size: 83px;
  }

  }

