


    .main
    {
        margin: 0 0 0 0;
        padding: 0 15% 0 15%;
        height: auto;
    
        .name-container
        { 
          
          text-align:center;
          animation-name: example;
          animation-duration: 3s;
          overflow: hidden;
          white-space: nowrap;
          height: auto;
          justify-content: center;
          display: flex;
          width: 100%;
      
          h1{
          
          color: $color-black-bright;
          font-family: $font-title;
          font-size: 80px;
          text-decoration:dashed;
          text-shadow: 1px 1px 2px blue;
      
          }
      
          
        }
    
        h1{
            text-align: center;
        }
    
        .top{
            display: flex;
            flex-direction: row;
            justify-content: space-around;
    
            .photo
            {
                box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
               
            }

            .photo:hover
            {
                scale: 1.2;
                transition: 0.5s;
            }
            .info1
            {
                width: 40%;
                h2{
                    color: $color-blue;
                    font-family: $font-title;
                }

                text-align: center;
                justify-content: center;
                display: flex;
                flex-direction: column;
                ul
                {
                    font-family: $font-text;
                    list-style: none;
                }
               
            }
    
    
        }
    
        .bottom
        {
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            text-align: center;
    
    
            font-family: $font-title;

            .hobbies{
                margin: 5% 0 0 0;
                box-sizing: border-box;
                h2{
                    font-family: $font-title;
                    color: $color-blue;
                    text-align: center;
                }

                ul{
                    list-style: none;
                    text-align: center;
                }
            }

            .pdf{
                margin: 8% 0 3% 0;
                display: flex;
                flex-direction: column;
                justify-content: space-between;
                text-align: center;
                

                i{
                    color: $color-blue;
                }

                a{
                    text-decoration: none;
                    font-family: $font-title;
                    color: $color-black-bright;
                    margin-top: 2em;
                    font-size: large;


                    
                }
            }
    
            
        }
    
    
    }

