.portfolioContent
{

    .radioDisplay{
        font-family: $font-title;
        list-style: none;
        display: flex;
        justify-content: center;
    }

    .projects{
        
        font-family: $font-text;
        justify-content: center;
        padding: 0 5% 0 5%;
        text-align: -moz-center;

        .project{
            border: solid;
            border-radius: 5px;
            margin: 2%;
            padding: 2%;
            width: 80%;
        }

        .project:hover{
            scale: 1.2;
        }



    }


    

}