.main{


    .formation{

        h2{
            font-family: $font-title;
            color: $color-orange;
        }

        .exp{
            padding: auto;
            font-family: $font-text;
            display: flex;
            flex-direction: row;
            font-family: $font-text;
            // border: 3px solid $color-blue;
            margin: 2%;
            padding: 1%;
            border-radius: 5px;
            background-color: $color-black-dark;
            color: $color-white;
            box-shadow: 5px 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
            height: 200px;


            .logo{
                width: 20%;
                box-shadow: 2px 4px 5px 0 rgb(1, 195, 234), 0 6px 20px 0 rgba(0, 0, 0, 0.19);


                img{
                    width: 100%;
                    object-fit: cover;
                    height: 100%;
                    max-height: 200px;
                    
                }
            }

            .description{
                width: 80%;
                text-align: center;
            }
            
        }

        .exp:hover{
            scale: 1.07;
            transition: 0.5;
        }

        }
    }




