
.languagesFrameworks
{
    width: 100%;
    font-family: $font-title;

    .languagesDisplay{
        width: 100%;
        margin: 5% 0 2% 0;

        h3{
            text-align: center;
        }
    }

    .frameworksDisplay{
        width: 100%;
        margin: 5% 0 2% 0;
        
        h3{
            text-align: center;
        }
    }

}

.years{
    font-size: .65rem;
    display:flex;
    justify-content:space-between;
    color:black;
    
    span{
        width: 36%;

        &:nth-child(2){
            text-align:center;
            width:20%;
        }

        &:nth-child(3){
            text-align:right;
            width:44%;
        }

        &:nth-child(2):before, &:nth-child(3)::before{
            content: '';
            background:rgba(180,179,179,0.5);
            position:absolute;
            height: 146px;
            width: 1px;
            transform: translate(9px,19px)
        }
    }

}

.languagesList{
    padding-top: .5rem;

    li{
        color: black;
    }

    .progressBar{
        height: .4rem;
        background: transparent;
        border-radius: .2rem;
        margin-top: .2rem;
        position: relative;

        &:after{
            content:'';
            position: absolute;
            background: $color-orange;
            border-radius: 50px;
            height: .4rem;
            width: 0%;
            animation: line 2s ease-out forwards;


            @keyframes line{
                to {
                    width: 100%;

                }

            }
        }
    }
}

.languages{

    h2{
        text-align: center;
        font-family: $font-title;
    }
    ul{
        list-style: none;
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: space-between;
        font-family: $font-text;

        li{
            width: 15%;
            
            
            img{
                width: 100%;
                height: 150px;
                box-shadow: 0px 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);

                
            }

            p{
                text-align: center;
            }
        }
        
    }
    

}