

.footer{

    
    height: 250px;
    background-color: $color-black-dark;
    

    .signature{
        margin-top: 2rem;
        text-align: center;
        letter-spacing: .1rem;
        font-size: .7rem;

        p{
            color:$color-white;

            &:hover{
                color: $color-white;
            }
        }
    }

    .network{
        padding: 2%;
        text-align: center;

        ul{
            list-style: none;

                       
            li{
                display: inline-block;
                margin: 0 35px 0 35px;

                a{
                    size: 5em;
                    color: $color-white;
                }

            }
        }
    }
}